import React from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Footer.css';
import classnames from 'classnames';

import slack from './sn/slack.svg';
import twitter from './sn/twitter.svg';
import facebook from './sn/facebook.svg';
import medium from './sn/medium.svg';
import reddit from './sn/reddit.svg';
import bitcoin from './sn/bitcoin.svg';


class Footer extends React.Component {
  render() {
    return (
      <div className={s.root}>
        <div className={s.container}>

          <div className={classnames(s.row, s.center_sm, s.center_xs)}>
            <div className={classnames(s.col_sm_2, s.col_xs_8)}>
              <a className={s.brand} href="https://blockpass.slack.com/">
                <img className={s.icon} src={slack} srcSet={`${slack} 2x`} alt='Slack' />
              </a>
            </div>
            <div className={classnames(s.col_sm_2, s.col_xs_8)}>
              <a className={s.brand} href="https://twitter.com/BlockpassOrg">
                <img className={s.icon} src={twitter} srcSet={`${twitter} 2x`} alt='twitter' />
              </a>
            </div>
            <div className={classnames(s.col_sm_2, s.col_xs_8)}>
              <a className={s.brand} href="https://www.facebook.com/BlockpassOrg-120936415138920/">
                <img className={s.icon} src={facebook} srcSet={`${facebook} 2x`} alt='Facebook' />
              </a>
            </div>
            <div className={classnames(s.col_sm_2, s.col_xs_8)}>
              <a className={s.brand} href="https://medium.com/@blockpass">
                <img className={s.icon} src={medium} srcSet={`${medium} 2x`} alt='Medium' />
              </a>
            </div>
            <div className={classnames(s.col_sm_2, s.col_xs_8)}>
                <img className={s.iconDisabled} src={reddit} srcSet={`${reddit} 2x`} alt='Coming soon' />
            </div>
            <div className={classnames(s.col_sm_2, s.col_xs_8)}>
                <img className={s.iconDisabled} src={bitcoin} srcSet={`${bitcoin} 2x`} alt='Coming soon' />
            </div>
          </div>

          <div className={classnames(s.row, s.center_xs)}>
            <div className={s.col_xs_8}>
              <p>© 2017 Blockpass. All rights reserved</p>
            </div>
          </div>

        </div>
      </div>
    );
  }
}

export default withStyles(s)(Footer);



