import Config_Test from './config_test.js';
import Config_Pre_ICO from './config_pre_ico.js';
import Config_ICO from './config_pre_ico.js';
import Config_After_ICO from './config_pre_ico.js';
import Config_Default from './config_default.js';

var curTime = new Date().getTime();

// Mon, 01 June 2017 00:00:00 GMT
var TIME_TEST_UTC = 1496275200000;
// Thu, 01 Jun 2017 00:00:00 GMT
var TIME_PRE_ICO = 1496275200000;
// Sat, 01 Jul 2017 00:00:00 GMT
var TIME_ICO = 1498867200000;
// Tue, 01 Aug 2017 00:00:00 GMT
var TIME_AFTER_ICO = 1501545600000;

var curConfig;
if (curTime <= TIME_TEST_UTC)
    curConfig = Config_Test;
else if (curTime <= TIME_PRE_ICO)
    curConfig = Config_Test;
else if (curTime <= TIME_PRE_ICO)
    curConfig = Config_Test;
else if (curTime <= TIME_ICO)
    curConfig = Config_Test;
else
    curConfig = Config_Default;

export default curConfig;



