import React, { PropTypes } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import {Card, CardActions, CardHeader, CardMedia, CardTitle, CardText} from 'material-ui/Card';
import s from './Countdown.css';

class Countdown extends React.Component {
    static propTypes = {
        title: PropTypes.string.isRequired,
        dueDate: PropTypes.number.isRequired,
    };

    constructor(props) {
        super(props);

        var curTime = new Date().getTime();
        var _secondsRemaining = (props.dueDate - curTime) / 1000;
        if (_secondsRemaining < 0)
            _secondsRemaining = 0;

        this.state = {
            secondsRemaining: _secondsRemaining,
            days: 0,
            hours: 0,
            minutes: 0,
            secs: 0
        };
    }

    componentDidMount()    {
        this.timerID = setInterval(
            () => this.tick(this),
            1000
        );
    }

    componentWillUnmount()    {
        clearInterval(this.timerID);
    }

    tick(self)    {
        try {
            var _secondsRemaining = self.state.secondsRemaining - 1;
            var _days = Math.floor(_secondsRemaining / 86400);
            var _hours = Math.floor((_secondsRemaining % 86400) / 3600);
            var _minutes = Math.floor((_secondsRemaining % 3600) / 60);
            var _secs = Math.floor(_secondsRemaining % 60);

            self.setState({
                secondsRemaining: _secondsRemaining,
                days: _days,
                hours: _hours,
                minutes: _minutes,
                secs: _secs
            });
            if (self.state.secondsRemaining <= 0) {
                clearInterval(self.timerID);
            }
        } catch (e)   {

        }
    }

    render() {
        return (
            <div style={{ backgroundColor: this.props.bgcolor }} className={s.root}>
                <div className={s.container}>
                    <row>
                        <div className={s.container}>
                            <h2 className={s.title}>{this.props.title}</h2>
                        </div>
                        <Card style={{width: 400, margin: 10}}>
                            <CardText>
                                <div className={s.container}>
                                    <div className={s.col_xs_3}>
                                        <div className={s.bannerTitle}>{this.state.days}</div>
                                        <div className={s.bannerDesc}>days</div>
                                    </div>
                                    <div className={s.col_xs_3}>
                                        <div className={s.bannerTitle}>{this.state.hours}</div>
                                        <div className={s.bannerDesc}>hours</div>
                                    </div>
                                    <div className={s.col_xs_3}>
                                        <div className={s.bannerTitle}>{this.state.minutes}</div>
                                        <div className={s.bannerDesc}>mins</div>
                                    </div>
                                    <div className={s.col_xs_3}>
                                        <div className={s.bannerTitle}>{this.state.secs}</div>
                                        <div className={s.bannerDesc}>secs</div>
                                    </div>
                                </div>
                            </CardText>
                        </Card>
                    </row>
                </div>
            </div>
        );
    }
}

export default withStyles(s)(Countdown);



