import React, { PropTypes } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Home.css';
import SectionConfig from '../config.js';
import Break from '../../components/Section_Break';
import Info from '../../components/Section_Info';
import Team from '../../components/Section_Team';
import Video from '../../components/Section_Video';
import Intro from '../../components/Section_Intro';

class Home extends React.Component {
  static propTypes = {
    news: PropTypes.arrayOf(PropTypes.shape({
      title: PropTypes.string.isRequired,
    })).isRequired,
  };

  render() {
    // mapping from string key to real Component class
    var sectionsMap = {
      'Break': Break,
      'Video': Video,
      'Team': Team,
      'Info': Info,
      'Intro': Intro,
    };

    // section list object
    var sectionList = [];
    // sectionList.push(React.createElement(Countdown, {key:9999, dueDate: 1492387200000, title: 'Count down to ICO start'}));
    // sectionList.push(React.createElement(Countdown, {key:10000, dueDate: 1493596800000, title: 'Count down to ICO end'}));
    sectionList.push(React.createElement(Intro, {key: 10000}));
    SectionConfig.sections.forEach((elem, idx) => {
      if (sectionsMap[elem.name] != undefined)  {
        elem.props['key'] = idx;
        sectionList.push(React.createElement(sectionsMap[elem.name], elem.props));
      }
    })

    // render function
    return (React.createElement('div', {className: s.root},
      React.createElement('div', {className: s.container}),
        sectionList
    ));
  }
}

export default withStyles(s)(Home);



