import React, { PropTypes } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import Paper from 'material-ui/Paper';
import s from './Team.css';

class Team extends React.Component {
  render() {
    return (
      <div className={s.root}>
        <div className={s.container}>
          <div className={s.col_xs_4}>
            <Paper className={s.avatar} zDepth={1} circle style={{ overflow: 'hidden' }}><img style={{ width: '100%', height: 'auto' }} src="http://vignette2.wikia.nocookie.net/main-cast/images/5/5b/Sorry-image-not-available.png/revision/latest?cb=20160625173435" /></Paper>
          </div>
          <div className={s.col_xs_4}>
            <Paper className={s.avatar} zDepth={1} circle style={{ overflow: 'hidden' }}><img style={{ width: '100%', height: 'auto' }} src="http://vignette2.wikia.nocookie.net/main-cast/images/5/5b/Sorry-image-not-available.png/revision/latest?cb=20160625173435" /></Paper>
          </div>
          <div className={s.col_xs_4}>
            <Paper className={s.avatar} zDepth={1} circle style={{ overflow: 'hidden' }}><img style={{ width: '100%', height: 'auto' }} src="http://vignette2.wikia.nocookie.net/main-cast/images/5/5b/Sorry-image-not-available.png/revision/latest?cb=20160625173435" /></Paper>
          </div>
        </div>
      </div>
    );
  }
}

export default withStyles(s)(Team);



